.personal-card {
  position: relative;
  width: 620px;
  max-width: calc(100vw - 16px * 2);
  padding-bottom: 40px;
  background-color: #fff;
  border-radius: 4px;
  text-align: center;
  color: #333;
}

.personal-card .photo {
  position: absolute;
  width: 120px;
  height: 120px;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, 0.2), 0 0 2px 0 rgba(0, 0, 0, 0.1);
  background: url(../../images/logo.jpg) no-repeat center center;
  background-size: 100%;
}

.personal-card .name {
  margin-top: 80px;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 2px;
}

.personal-card .headline {
  margin-top: 5px;
  font-size: 16px;
  line-height: 1.2;
}

.personal-card .guide-button {
  margin: 30px 20px;
}

.personal-card .guide-button a {
  display: block;
  margin: 0 auto;
  max-width: 400px;
  font-size: 18px;
  line-height: 48px;
  box-shadow: inset 0 0 0 100px hsla(0, 0%, 100%, 0);
  background-color: rgb(48, 91, 144);
  border-color: rgb(48, 91, 144);
  border-radius: 5px;
  color: #fff;
  text-decoration: none;
  transition: all 0.15s ease-out;
}

.personal-card .guide-button a:hover {
  opacity: 0.5;
}

.personal-card .bio {
  margin: 0 auto;
  max-width: 400px;
  padding: 0 20px;
  font-size: 16px;
  text-align: left;
}

.personal-card .bio p {
  margin-bottom: 12px;
}

.personal-card .bio p:last-child {
  margin-bottom: 0;
}
