body {
  font-family: Proxima Nova, Tahoma, Helvetica, Verdana, sans-serif;
  line-height: 1.5;
  background-color: #305b90;
  background: linear-gradient(135deg, #305b90 0%, #305b90 30%, #529bf5 100%)
    fixed;
}

.app {
  box-sizing: border-box;
  min-height: 100vh;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

